import React from "react";

export class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { error: null };
  }

  componentDidCatch(error) {
    this.setState({ error });
  }

  reloadPage = () => {
    window.location.reload();
  };

  render() {
    const { error } = this.state;
    if (error) {
      return (
        <div className="alert alert-warning" role="alert">
          <h3>Ha ocurrido un error por favor volver a cargar la p√°gina</h3>
          <p>{error.toString()}</p>
          <button onClick={this.reloadPage} className="btn btn-light mt-4">Recargar p√°gina</button>
        </div>
      );
    }
    return this.props.children;
  }
}
