import React , {useContext, useState} from "react";

//components
import { Image } from "react-bootstrap";
//Lazy Loading
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css'

import { Modal,  Typography } from '@mui/material';
//MaterialUI
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Box from '@mui/material/Box';
import Rating from '@mui/material/Rating';
import '../../styles/styles.css'
import capitalizarNombre from "../functions/capitalizarNombre";
import Context from "../context/UserContext";
import SignIn from "../users/login";
const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(3),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    color: "white",
  },
  imagenCarousel: {
    cursor:"pointer",
    minWidth: "100%",
    objectFit: "contain",
    maxHeight: "85vh",
    objectPosition: "top",
  },
  carouselText: {
    position: "absolute",
    bottom: 0,
    left: 0,
    width: "100%",
    background: "linear-gradient(to bottom, transparent 20%, rgba(24, 26, 54, 0.95) 90%)",
    padding: "300px 20px 20px",
  },
  textTitle:{
    fontSize:"27px"
  },

  "@media (max-width: 500px)": {
    imagenCarousel: {
      minWidth: "100%",
      objectFit: "cover", // Cambia a 'cover' para que ocupe m√°s espacio
      maxHeight: "100vh", // Aumenta la altura en dispositivos peque√±os
      objectPosition: "top",
    },
    carouselText: {
      padding: "120px 20px 10px", // Reduce el padding del t√≠tulo para que no tape la imagen
      bottom:"-24px",
      zIndex:"10"
    },
    textTitle:{
      fontSize:"18px"
    },
    containerRating:{
      display:"none"
    }
    
  },
}));


const changeDuration = (duration) => {
  let tiempo = duration.split(':');
  let new_duration = "";
  if (tiempo) {
    if (tiempo[0] !== "00") {
      new_duration = tiempo[0] + "h";
    }
    if (tiempo[1] !== "00") {
      new_duration = " " + new_duration + " " + tiempo[1] + "min";
    }
    /* if (tiempo[2] !=="00") {
      new_duration = " "+new_duration+" "+tiempo[2]+"s";
    } */
  }
  return new_duration.trim(); // trim() para eliminar cualquier espacio en blanco adicional
};
/**
 * Componente para mostrar en el carousel los videos con algunos campos.
 * @param {*} video
 * @returns Item de un video.
 */
const VideosItem = ({ video,...props } ) => {
  
  const [open, setOpen] = useState(false);

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const {user} = useContext(Context)
  const classes = useStyles();
  return (
    <div  id="contenedorItemVideo" className="cursorPointer contenedorItemVideo">
      <div
  className={"" + classes.row}
  onClick={() => {
    if (user || props.webinars) {
      const categoria13 = video.categorias.find(cat => cat.id === 13);
      props.verVideo(video.id, categoria13 ? 13 : null);
    } else {
       handleOpen() // Esto podr√≠a ir a seeVideo sin historial
    }
  }}
>

        <div className={" "+classes.imagen}>
            <Image
              src={"https://sccotview.com" + video.featured_image}
              className={classes.imagenCarousel +" "}
              
            >
            </Image>
          
        </div>
        <div className=" col-12  ">
        <div className={classes.carouselText }>
          <div className="d-flex justify-content-end text-capitalize  ">
            <Box sx={{'& > legend': { mt: 2 }}} className={classes.containerRating}>
              <div className="bg-secondary d-inline pt-2 px-1 rounded-2">
                <Rating name="read-only" value={parseFloat(video.score)} precision={0.5} readOnly />
              </div>
            </Box>

          </div>
          <h3 className={`${classes.textTitle} text-light text-capitalize text-start border-bottom border-white pb-2`}>
            {video.title_espanol}
          </h3>

          {/* <span className="text-light text-start font-weight-light text-capitalize truncate-text-6">{video.description_esp}</span>
          {video.categorias.length > 0 && (
            <ul className="d-flex flex-wrap justify-content-start p-0 m-0 py-0 mt-2  text-light">
              <span className="text-capitalize me-1">{video.categorias.length > 1 ? "Categor√≠as" : "Categor√≠a"} ‚Üí </span>
              {video.categorias.map(categoria => (
                <li key={categoria.id} className="list-group-item list-group-item-light px-2 rounded-4 mx-1"><small className="text-capitalize">{categoria.categoria}</small></li>
              ))}
            </ul>
          )} */}
      {video.autor.length > 0 && (
        <ul className="d-flex flex-wrap justify-content-start p-0 text-light">
        <div className="d-flex justify-content-between w-100 mb-2">
          <div>

        <span className="text-capitalize small">
          {video.autor.length > 1 ? "Autores" : "Autor"} ‚Üí
        </span>

          {video.autor.slice(0, 2).map((autor, index, array) => (
            <p key={autor.id} className="px-1 rounded-4 m-0 d-inline">
              <small className="text-capitalize " style={{ fontSize: "0.85rem" }}>
                {capitalizarNombre(autor.autor)}
                {index < array.length - 1 ? "," : ""}
              </small>
              
            </p>
            
          ))}
          {video.autor.length > 2 && (
            <span className="px-2 small" style={{ fontSize: "0.85rem" }}>...</span>
          )}
          </div>
          


          <p className="mx-1" style={{ fontSize: "0.85rem" }}>{changeDuration(video.duration)}</p>
        </div>

        
      </ul>
      )}
          </div>
        </div>
      </div>
      <Modal open={open} onClose={handleClose}>
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            width: 500,
            borderRadius: 2,
            boxShadow: 24,
            outline:'none',
            p: 4,
            zIndex:2
          }}
        >
          <SignIn onClose={handleClose} open={open} setOpen={setOpen}></SignIn>
        </Box>
      </Modal> 
    </div>
  );
};

export default VideosItem;
