/* eslint-disable array-callback-return */
import React, { useEffect, useState,useContext } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import {Navigation } from "swiper/modules";
import getTipoVideo from '../../services/tipoVideo'

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import Context from "../context/UserContext";
import Box from '@mui/material/Box';
import { Modal,  Typography } from '@mui/material';

import { LazyLoadImage } from "react-lazy-load-image-component";
import Rating from "@mui/material/Rating";
import { Container } from "@material-ui/core";

import SignIn from "../users/login";

const VideosCategoriaFila = ({ categoria, videos, webinarsCondition, verVideo,search }) => {
  
  const {user} = useContext(Context)
  const changeDuration = (duration) => {
    let tiempo = duration.split(":");
    let new_duration = "";

    if (tiempo) {
      if (tiempo[0] !== "00") {
        new_duration = tiempo[0] + "h";
      }
      if (tiempo[1] !== "00") {
        new_duration = " " + new_duration + " " + tiempo[1] + "min";
      }
    }
    return new_duration.trim();
  };
  const [open, setOpen] = useState(false);

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
    useEffect(()=>{
      // console.log(videos);
      // console.log(webinarsCondition);
      // console.log("verVideo en VideosCategoriaFila:", verVideo);

      
    },[])
  if (!categoria) return null;

  const videosEnCategoria =
    videos &&
    search(videos).filter((video) =>
      video.categorias.some((element) => categoria.categoria === element.categoria)
    );

  // Creamos un identificador √∫nico basado en el nombre de la categor√≠a
  const categoriaId = `swiper-${categoria.categoria.replace(/\s+/g, "-").toLowerCase()}`;
    
  return (
    <Container>
      {videosEnCategoria.length > 0 && (
        <div>
          <div className="d-flex align-items-center">
            <h3 className="me-3">| {categoria.categoria} <small>‚ñ∂</small></h3>
            <small className="border-bottom border-light mb-2">{videosEnCategoria.length} Videos</small>
          </div>

          {/* Botones personalizados con identificador √∫nico */}
          <div className="position-relative">
            <button className={`custom-prev-${categoriaId} swiper-button-prev`} style={styles.navButton}></button>
            <button className={`custom-next-${categoriaId} swiper-button-next`} style={styles.navButton}></button>

            <Swiper
              spaceBetween={30}
              loop={true}
              breakpoints={{
                1400: { slidesPerView: 4, spaceBetween: 25, slidesPerGroup:2 },
                1200: { slidesPerView: 3, spaceBetween: 5 , slidesPerGroup:2},
                992: { slidesPerView: 2.5, spaceBetween: 5 , slidesPerGroup:1},
                768: { slidesPerView: 2.2, spaceBetween: 5 , slidesPerGroup:1},
                576: { slidesPerView: 1.5, spaceBetween: 5 , slidesPerGroup:1}, // 1.3 para que asome la siguiente tarjeta
                376: { slidesPerView: 1.2, spaceBetween: 10 , slidesPerGroup:1}, // 1.3 para que asome la siguiente tarjeta
              }}
              navigation={{
                prevEl: `.custom-prev-${categoriaId}`,
                nextEl: `.custom-next-${categoriaId}`,
              }}
              modules={[ Navigation]}
              className="mySwiper"
            >
              {videosEnCategoria.map((video) => (
                
                
                <SwiperSlide key={video.id} >
                  <div
  onClick={() => {
    if (user || webinarsCondition) {
      // Buscar si el video tiene categor√≠a 13
      const categoria13 = video.categorias.find(cat => cat.id === 13);
      verVideo(video.id, categoria13 ? 13 : null);
    } else {
      handleOpen()
    }
  }}
  className="card cursorPointer card-video px-2"
>


                  <small className="d-flex justify-content-end px-3 pt-1  ">
                     {video.tipe_of_video  == 1 && "video" }
                     {video.tipe_of_video  == 2 && "serie" }
                     {video.tipe_of_video  == 3 && "caso" }
                  </small>
                    <LazyLoadImage
                      effect="blur"
                      src={`https://sccotview.com${video.featured_image}`}
                      className="card-img-top cursorPointer py-1 "
                      alt="..."
                      placeholder={
                        <div style={styles.placeholder}>
                        </div>
                      }
                    />
                    <p className="truncate-text">{video.title_espanol}</p>
                    <div className="d-flex justify-content-between align-items-end" onClick={(event) => event.stopPropagation()}>
                      <div>
                        <p>{changeDuration(video.duration)}</p>
                      </div>
                      <div className="bg-secondary rounded-2 mb-2 pt-1">
                        <Rating name="read-only" value={parseFloat(video.score)} precision={0.5} readOnly />
                      </div>
                    </div>
                  </div>
                </SwiperSlide>
              ))}
            </Swiper>
          </div>
        </div>
      )}
      <Modal open={open} onClose={handleClose}>
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            width: 500,
            borderRadius: 2,
            boxShadow: 24,
            outline: 'none',
            p: 4,
            zIndex: 2
          }}
        >
          <SignIn onClose={handleClose} open={open} setOpen={setOpen}  />
        </Box>
      </Modal>
    </Container>
  );
};

const styles = {
  navButton: {
    transform: "translateY(-50%)",
    backgroundColor: "rgba(0, 0, 0, 0.6)",
    color: "white",
    border: "none",
    borderRadius: "50%",
    width: "50px",
    height: "50px",
    fontSize: "24px",
    cursor: "pointer",
    transition: "background 0.3s",
    zIndex: 10,
  },
  placeholder: {
    width: "100%",
    height: "200px", // Ajusta seg√∫n el tama√±o de tus im√°genes
    backgroundColor: "transparent", // Color gris claro
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    fontSize: "14px",
    fontWeight: "bold",
    color: "#666",
    borderRadius: "8px",
  },
}

export default VideosCategoriaFila;
