import React, { useEffect, useState } from 'react'
import Rating from '@mui/material/Rating';
import getTipoVideo from '../../services/tipoVideo'
import cx from "classnames";
import SliderContext from "../context/SliderContext";
import ShowDetailsButton from '../slider/ShowDetailsButton';
//Lazy loading
import { LazyLoadImage } from 'react-lazy-load-image-component';
import "../slider/Item.scss";
import Button from "@material-ui/core/Button";

import 'react-lazy-load-image-component/src/effects/blur.css'
import { Container } from '@mui/material';

const VideoListSearch = ({ categoria, verVideo, ...props }) => {
  const [duracion, setDuracion] = useState([]);
  const [showDetails, setShowDetails] = useState(false);
  const [tipoVideo, setTipoVideo] = useState('')

  const changeDuration = (duration) => {
    let tiempo = duration.split(':');
    let new_duration = "";
    if (tiempo) {
      if (tiempo[0] !== "00") {
        new_duration = tiempo[0] + "h";
      }
      if (tiempo[1] !== "00") {
        new_duration = " " + new_duration + " " + tiempo[1] + "min";
      }
      /* if (tiempo[2] !=="00") {
        new_duration = " "+new_duration+" "+tiempo[2]+"s";
      } */
    }
    return new_duration.trim(); // trim() para eliminar cualquier espacio en blanco adicional
  };
  
  /* const handleTipoVideo =  async () =>{
    let idTipoVideo = video.tipe_of_video
    const response = await getTipoVideo(idTipoVideo)
    setTipoVideo(response)

  } */

    useEffect(()=>{
    },[])
  if (categoria) {
    const videosEnCategoria = props.videos.filter((video) => (
      video.categorias.some((element) => categoria.categoria === element.categoria )
    ));

    return (
      <div className='col-xl-9 mx-auto'> 
        {videosEnCategoria.length > 0 && (
          <div className="">
            <div className="d-flex align-items-center ">
              <h3 className="mx-3" key={categoria.id}>| {categoria.categoria}<small > ‚ñ∂ </small> </h3>
            </div> 
            <div className="d-flex justify-content-center flex-wrap">
              {
                videosEnCategoria.map((video)=>{
                  return(

            <div key={video.id} className="card cursorPointer card-video">
            <small className="d-flex justify-content-end px-3 pt-1 ">
              {video.tipe_of_video == 1 ? "Video": 
                video.tipe_of_video == 2 ? "Serie" : 
                video.tipe_of_video == 3 ? "Caso" : ""  }
            </small>
              
              <Button onClick={() => verVideo(video.id) }> {/* NO ME INTRA AL VIDEO  ARREGLAR EL BUSCAR QUE TAMPOCO SIRVE */}
                <LazyLoadImage effect="blur" src={`https://sccotview.com${video.featured_image}`} className="card-img-top cursorPointer" alt="..." />
              </Button>
              
              <div className="card-body p-0 px-2 d-flex flex-column justify-content-between" key={video.id}>
                <p className="truncate-text" onClick={(event) => {event.stopPropagation(); }}>
                  {video.title_espanol}
                 {/*  {console.log(video)} */}
                </p>
                <div className="d-flex justify-content-between align-items-end" onClick={(event) => event.stopPropagation()}>
                  <div>
                    <p>{changeDuration(video.duration)}</p>
                  </div>
                  <div className="bg-secondary rounded-2 mb-2 pt-1" onClick={(event) => event.stopPropagation()}>
                    <Rating name="read-only" value={parseFloat(video.score)} precision={0.5} readOnly />
                  </div>
                </div>
              </div>
            </div>
                  )
                })
              }
            </div>
          </div>
        )}
      </div>
    );
  }
  

}

export default VideoListSearch