import { useEffect, useState } from "react";
import Swal from "sweetalert2";
import FilterAltIcon from "@mui/icons-material/FilterAlt";
import "../../index.css";
import { IconButton, Button } from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import ClearIcon from "@mui/icons-material/Clear";

const SearchComponent = (props) => {
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("All");
  const [searchTerm, setSearchTerm] = useState(props.query); // Estado local para el input
  const [btnQuitarFiltro, setBtnQuitarFiltro] = useState(false);

  // const handleClickOpen = () => {
  //   setOpen(true);
  // };

  // const handleClose = () => {
  //   setOpen(false);
  // };
  useEffect(() => {
    setCategories(props.categories);
  }, [props.categories]);

  const handleCategoryChange = (e) => {
    const newCategory = e.target.value;
    setSelectedCategory(newCategory);
    props.setFilterParam(newCategory);
    setSearchTerm(""); // Limpiar el input
    props.setQuery(""); // Limpiar el estado global del buscador
    setBtnQuitarFiltro(newCategory !== "All"); // ‚úÖ Activar bot√≥n si la categor√≠a no es "All"
    Swal.close();
  };

  const handleInputChange = (e) => {
    setSearchTerm(e.target.value); // Solo actualiza el estado local sin buscar
  };

  const executeSearch = () => {
    setBtnQuitarFiltro(true); // ‚úÖ Activar bot√≥n al hacer una b√∫squeda
   // console.log("üîç Buscando:", searchTerm);
    props.setQuery(searchTerm); // Ahora s√≠ ejecuta la b√∫squeda con el t√©rmino ingresado
  };

  const clearFilters = () => {
    setSelectedCategory("All");
    setSearchTerm("");
    props.setFilterParam("All");
    props.setQuery("");
    setBtnQuitarFiltro(false); // ‚úÖ Desactivar bot√≥n cuando se limpian los filtros
  };

  const openFilterModal = () => {
    Swal.fire({
      title: "Filtrar por Categor√≠a",
      html: `
        <select id="category-select" class="form-control" onchange="window.handleCategoryChange(event)">
          <option value="All">Todas las categor√≠as üî†</option>
          ${categories
            .slice()
            .sort((a, b) => a.categoria.localeCompare(b.categoria))
            .map((categ) => `<option value="${categ.categoria}">${categ.categoria}</option>`)
            .join("")}
        </select>
      `,
      showCancelButton: false,
      showConfirmButton: false,
      didOpen: () => {
        window.handleCategoryChange = handleCategoryChange;
      }
    });
  };

  return (
    <div className="wrapper mb-5 pb-5 mt-3">
      <div className="search-wrapper col-sm-12 d-lg-flex justify-content-start align-items-center">
        <div className="d-flex border border-light rounded overflow-hidden bg-light container-search-shadow">
          <input
            type="search"
            name="search-form"
            id="search-form"
            className="form-control border-0 bg-light px-3 py-0"
            placeholder="Buscas algo?"
            value={searchTerm}
            onChange={handleInputChange} // ‚úÖ Ahora solo guarda el texto pero no busca autom√°ticamente
            onKeyDown={(e) => e.key === "Enter" && executeSearch()}
            title="Buscar"
          />
          <IconButton
            aria-label="buscar"
            size="large"
            className="text-light"
            title="Buscar"
            onClick={executeSearch} // ‚úÖ Solo busca cuando se da clic
            style={{ backgroundColor: "#3f51b5", borderRadius: 0, width: "50px", height: "50px" }} // üî≥ Cuadrado
          >
            <SearchIcon fontSize="inherit" />
          </IconButton>

          <IconButton
            aria-label="filtrar"
            size="large"
            className="bg-light"
            title="Filtrar por categor√≠a"
            onClick={openFilterModal}
            style={{ color: "#3f51b5" }}
          >
            <FilterAltIcon fontSize="inherit" />
          </IconButton>
        </div>

        {/* üî¥ BOT√ìN QUITAR FILTRO (solo si hay un filtro aplicado) */}
        {btnQuitarFiltro && (
          <Button
            variant="contained"
            color="secondary"
            onClick={clearFilters}
            startIcon={<ClearIcon />}
            style={{ marginLeft: "10px" }}
          >
            Quitar Filtro
          </Button>
        )}
      </div>
    </div>
  );
};

export default SearchComponent;
