import Swal from "sweetalert2";
import axiosInstance from "../axios";

const API_URL = "https://sccotview.com/api/gustos_by_users/";

/**
 * Funci√≥n para agregar preferencias(gustos) de un usuario.
 * @param {formData} newPreferencia 
 * @returns La data enviada desde el backend.
 */
export const RegisterPreferenciaUser = async (newPreferencia) => {
    const response = await axiosInstance.post(API_URL, newPreferencia);
    if (response.status === 200) {
      return await response.data;
    }
  };
/**
 * Funci√≥n para consultar todas las preferencias de usuario.
 * @returns La data enviada desde el backend.
 */
export const ListPreferencia = async () => {
    const response = await axiosInstance.get(`${API_URL}`);
    if (response.status === 200) {
      return await response.data;
    }
  };
  /**
   * funci√≥n para obtener una preferencia en espec√≠fico.
   * @param {pk} histID 
   * @returns La data enviada desde el backend.
   */
export const getPreferencia = async (ID) => {
    const response = await axiosInstance.get(`${API_URL}${ID}`);
    if (response.status === 200) {
      return await response.data;
    }
  };
  /**
   * funci√≥n para obtener una preferencia de un usuario en espec√≠fico.
   * @param {fk} idUser 
   * @returns La data enviada desde el backend.
   */
  export const ListPreferenciaUser = async (idUser) => {
    try {
      const response = await axiosInstance.post(`${API_URL}list_by_user/`, { id: idUser });
  
      if (response.status === 200) {
        return response.data;
      } else {
        console.warn("Respuesta inesperada:", response.status);
        return null;
      }
    } catch (error) {
      console.error("Error en ListPreferenciaUser:", error);
      return null;
    }
  };
  

/**
 * funci√≥n para obtener actualizar la preferencia de un usuario.
 * @param {*} id 
 * @param {*} updatePU 
 * @returns La data enviada desde el backend.
 */
export const updatePreferenciaUser = async(id,updatePU) =>{
    const response = await axiosInstance.patch(API_URL+id+"/",updatePU);
    if (response.status === 200) {
        return response.data;
    }
}
export const deletePreferencia = async (id) => {
  const response = await axiosInstance.delete(`${API_URL}${id}`);
  if (response.status === 200) {
    Swal.fire({
      title: response.data.message,
      icon: "success"
    }).then(res=>{
      window.location.reload();
    }).catch(()=>{
      Swal.fire({
        title: "Lo sentimos no fue posible eliminar la preferencia, intenta nuevamente mas tarde",
        icon: "warning"
      })
    })
    
    return await response.data;
  }
};